import Vue from 'vue'
import Router from 'vue-router'
import tabs from '../components/tabs'
import sail from '../components/sail'
import login from '../components/login'
import detail from '../components/detail'
import help from '../components/help'
import store from '../store'

Vue.use(Router)
const router=new Router({
  routes: [
    {
      path:'/tabs',
      component:tabs
    },
    {
      path:'/sail',
      component:sail,
      // meta: {
      //   requiresAuth: true,  // 
      // }
    },
    {
      path:'/login',
      component:login
    },
    {
      path:'/home',
      component:login
    },
    {
      path:'/detail/:id',
      component:detail,
      // meta: {
      //   requiresAuth: true,  // 
      // }
    },
    {
      path:'/help',
      component:help
    }

    
    
  ]
})
//
router.beforeEach((to, from, next) => {
  //storetoken
  let token = store.state.token;
  //requiresAuth
  if (to.meta.requiresAuth) {
    console.log("")
   if (token) {
     console.log(token)
    next();
   } else {
    next({
     path: '/login',
     query: { redirect: to.fullPath } // path
    });
   }
  } else {
   next(); 
  }
 });
 
export default router;



