<template>
    <div class="help">
<el-steps :active="active" align-center>
  <el-step title="1"  description="" @click="change"></el-step>
  <el-step title="2" description="
    "></el-step>
  <el-step title="3" description="
  "></el-step>
  <el-step title="4" description="
  "></el-step>
</el-steps>
<div>
    <el-button style="margin-top: 50px;" @click="change"></el-button>
</div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            active:1
        }
    },
    methods:{
        change(){
            if (this.active++ > 3) this.active = 0;
        },
    }
}
</script>
<style>
    .help{
        margin-top: 100px;
    }
</style>





